.loginPage {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
}

.loginPage .centered {
    display: flex;
    width: 100%;
    flex-flow: column;
    align-items: center;
    margin: 8px;
}

.loginPage .title {
    color: rgb(255, 255, 255);
    font-size: 17px;
    font-style: normal;
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 8px;
}

.loginPage .loginButton {
    background-color: rgb(29,161,242); /* Green */
    border: none;
    color: white;
    padding: 8px;
    width: 100%;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}