.tweet {
    height: 100vh;
    padding-left: 8px;
    padding-right: 8px;
    display: flex;
    flex-flow: row;
}

.tweet span {
    color: rgb(255, 255, 255);
}

.tweet .col1 {
    margin-top: 30px;
    display: flex;
    flex-flow: column;
}

.tweet .col1 img {
    height: 35px;
    width: 35px;
    margin-right: 8px;
}

.tweet .col2  {
    margin-top: 30px;
    display: flex;
    flex-flow: column;
    align-content: flex-start;
    
}

.tweet .col2 .row1 {
    display: flex;
    flex-flow: row;
    align-items: center;
}

.tweet .col2 .row1 .name {
    font-size: 17px;
    font-stretch: 100%;
    font-style: normal;
    font-family: Arial, Helvetica, sans-serif;
}
.tweet .col2 .row1 .time {
    font-size: 14px;
    color: rgb(136, 153, 166);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-left: 8px;
}

.tweet .col2 .row2 {
    display: flex;
    flex-flow: row;
    align-items: flex-start;
}

.tweet .col2 .row2 .screenName {
    font-size: 14px;
    color: rgb(136, 153, 166);
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.tweet .col2 .row3 {
    display: flex;
    flex-flow: row;
    margin-top: 4px;
    align-items: flex-start;
}

.tweet .col2 .row3 span {
    text-align: left;
    font-size: 13px;
    font-stretch: 100%;
    font-style: normal;
    font-family: Arial, Helvetica, sans-serif;
}

.tweet img {
    margin-top: 8px;
    width: 100%;
}